import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { User } from '../user';
import { UserService } from '../user.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit {
  
  token = [];
  input = {
    username: '',
    password: ''
  };

  constructor(
    private appUser: UserService,
    private router: Router,
    public user: User
    ) { 
      if (sessionStorage.getItem('currentUser') != null)
      {
        this.appUser.setLoggedIn(true);
        this.router.navigate(['main']);
      }
      else
      {
        this.appUser.setLoggedIn(false);
        this.router.navigate(['']);
      }
    }

  ngOnInit() : void {
      this.input = {
      username: '',
      password: ''
    };

    
  }

  register() {
    this.router.navigate(['register'])
  }

  onLogin() {
    this.appUser.loginUser(this.input).subscribe(
      response => {
        this.user.username = this.input.username;
        this.token.push(response);
        sessionStorage.setItem('token', JSON.stringify(this.token));
        this.appUser.setLoggedIn(true);
        this.appUser.getUser(this.input.username).subscribe(
          data => {
            sessionStorage.setItem('currentUser', JSON.stringify(data));
            this.user.id = data[0].id;
            this.user.firstName = data[0].first_name;
            this.user.lastName = data[0].last_name;
            this.user.username = data[0].username;
            this.user.isSuperuser = data[0].is_superuser;
            this.router.navigate(['main']);
          },
          error => {
            console.log(error);
          }
        )
      },
      error => {
        console.log('error', error);
        alert('Incorrect username or password. Please try again!')
      }
    );
  }  
}