import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { User } from './user';

@Injectable({
  providedIn: 'root'
})
export class TodoService {

  // baseUrl = 'http://127.0.0.1:8000';
  baseUrl = 'http://todolist-todolist.7e14.starter-us-west-2.openshiftapps.com';
  HttpHeaders = new HttpHeaders({'Authorization': 'Token ebd1b536f6acef41d13946aa7605f9c6bc03ce35'});

  constructor(
    private http: HttpClient,
    public user: User,
    ) {
      // this.appUser.getCurrentToken();
      // console.log(this.HttpHeaders.get('Authorization'))
    }
    

  getTodosByUser(userId): Observable<any> {
    return this.http.get(this.baseUrl + '/api/todos/?user=' + userId, {headers: this.HttpHeaders});
  }

  getAllTodos(): Observable<any> {
    return this.http.get(this.baseUrl + '/api/todos/', {headers: this.HttpHeaders});
  }

  getTodo(id): Observable<any> {
    return this.http.get(this.baseUrl + '/api/todos/' + id + '/', {headers: this.HttpHeaders});
  }

  completeTodo(todo, status): Observable<any> {
    const body = {is_complete: status};
    return this.http.patch(this.baseUrl + '/api/todos/' + todo.id + '/', body, {headers: this.HttpHeaders});
  }
  

  updateTodo(todo): Observable<any> {
    const body = {title: todo.title};
    return this.http.patch(this.baseUrl + '/api/todos/' + todo.id + '/', body, {headers: this.HttpHeaders});
  }

  createTodo(text, userId): Observable<any> {
    const body = {title: text, user: userId};
    return this.http.post(this.baseUrl + '/api/todos/', body, {headers: this.HttpHeaders});
  }

  deleteTodo(id): Observable<any> {
    return this.http.delete(this.baseUrl + '/api/todos/' + id + '/', {headers: this.HttpHeaders});
  }

  todoImage(id, fileToUpload, name): Observable<any> {
    const formData: FormData = new FormData();
    formData.append('taskImage', fileToUpload, name);
    return this.http.patch(this.baseUrl + '/api/todos/' + id + '/', formData, {headers: this.HttpHeaders});
  }


}