import { Component, OnInit, NgZone } from '@angular/core';
import { Task } from '../task';
import { TodoService } from '../todo.service';
import { UserService } from '../user.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-task',
  templateUrl: './task.component.html',
  styleUrls: ['./task.component.css'],
})
export class TaskComponent implements OnInit {
  isLoaded= false;
  taskId = {id: null, taskImage: null};
  task;
  files: any;
  editing =false;
  error = false;
  enlarge = false;

  constructor(
    private todoService: TodoService,
    public taskClass: Task,
    private appUser: UserService,
    private router: Router,
  ) { 

  }

  ngOnInit(){
        if (sessionStorage.getItem('currentUser') != null)
        {
          this.appUser.setLoggedIn(true);
          if (sessionStorage.getItem('task') != null)
            this.task = JSON.parse(sessionStorage.getItem('task'));
            console.log(this.task);
            this.getTask(this.task.id);
            if(this.task != null)
              this.isLoaded = true;
            else
              this.isLoaded = false;
        } 
        else
        {
          this.appUser.setLoggedIn(false);
          this.router.navigate(['']);
        }
  }

  cancelEditing() {
    this.editing = false;
  }

  editTitle(){
    this.editing = true;
  }

  enlargeImage(){
    this.enlarge = true;
  }

  dropped(event) {
    event.preventDefault();
    this.files = event.dataTransfer.files;
    if(this.files.length > 0 && this.files[0].type.includes("image/") == true)
    {  
      this.error = false;
      console.log(this.files);
      this.uploadImage(this.files[0], this.files[0].name);
    }
    else
      this.error = true;
      console.log(this.files);
  }
  
 

  uploadImage(file, path) {
    this.todoService.todoImage(this.taskClass.id, file, path).subscribe(
      data => {
        this.getTask(this.taskClass.id);
      },
      error => {            
        console.log(error);
      }
    );
  }

  getTask(id){
    this.todoService.getTodo(id).subscribe(
      data => {
        this.task = data;
        console.log(this.task)
        this.appUser.getUsername(this.task.user)          
            .subscribe(
              data => {
                this.task.username = data[0].username.toString();
              }
            )
        if(this.task.is_complete)
          this.task.status = 'Completed'
        else
          this.task.status = 'Has not completed'
      },
      error => {            
        console.log(error);
      }
    );
  }

  fileOver(event){
    event.stopPropagation();
    event.preventDefault();
  }

  updateTodo(task, editedTitle) {
    this.editing = false;
    if (editedTitle.length < 8){
      alert('Please input what you want to update to, '
        + 'it needs to be at least 8 characters')
    }
    else {
      task.title = editedTitle;
      this.todoService.updateTodo(task).subscribe(
        data => {
          this.getTask(this.task.id);
        },
        error => {            
          console.log(error);
        }
      );
    }  
  }


  goBack() {
    this.router.navigate(['main']);
  }

  closeEnlarge() {
    this.enlarge = false;
  }
}
